import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController,
   NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-unidadplan-create',
  templateUrl: 'unidadplan-create.html',
})
export class unidadplanCreatePage {

  
  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtipo3: string="";
  public idtipo2: string="";
  public idnombre: string="";
  public idnombre2: string="";
  public idcodigo2: string="";
  public idnombre3: string="";
  idempresa:any;
  
  public idedificio                 : any;

  public idedificio2		  :any=[];
  public idsalon2		  :any=[];

  public codfac               : any;
  public idsalon               : any;
  public horario                : any;
  public idmateria                : any;
  public capacidad                : any;
  public asignados                : any;
  public fexamen                : any;
  public tcapacidad                : any;
  public vgrupo  :any=[];
  public vgrupo2  :any=[];
  public vgrupo3  :any=[];


  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
    
     this.form = this._FB.group({
       idedificio     : ['', Validators.required],
       idsalon     : ['', Validators.required],
       horario     : ['', Validators.required],
       idmateria     : ['', Validators.required],
       capacidad     : [''],
       asignados     : [''],
       codfac     : [''],
       fexamen     : ['', Validators.required]
   
      
       
     });

      
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {   
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idtipo3=this.navParams.data.record2.idtipo3;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.idnombre2=this.navParams.data.record2.idnombre2;
    this.idcodigo2=this.navParams.data.record2.idcodigo2;
    this.idnombre3=this.navParams.data.record2.idnombre3;

      
  this.authService.getReg(this.idtipo+'/'+ this.idtipo2,'unidadedificios').then((result) => {
    this.vgrupo=result;
  }, (err) => {  
  this.msj.showError3('Error',err.error);
});


this.authService.getReg(this.idtipo+'/'+ this.idtipo2,'facultadmaterias').then((result) => {
 
 if(result)
 {
    if(result[0].fisica)   {this.vgrupo3.push({nombre:'Fisica'});}
    if(result[0].matematica)   {this.vgrupo3.push({nombre:'Matematica'});}
    if(result[0].lenguaje)   {this.vgrupo3.push({nombre:'Lenguaje'});}
    if(result[0].biologia)   {this.vgrupo3.push({nombre:'Biologia'});}
    if(result[0].quimica)   {this.vgrupo3.push({nombre:'Quimica'});}

 }
  
}, (err) => {  
this.msj.showError3('Error',err.error);
});


    if(this.navParams.get("record")==null)
    {

    
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.idedificio=	this.navParams.data.record.idedificio.id +','+this.navParams.data.record.idedificio.nombre;
       this.idedificio2=this.idedificio.split(',');
       this.authService.getReg(this.idtipo+'/'+ this.idtipo2+'/'+  this.navParams.data.record.idedificio.id,'unidadedificiosalons').then((result) => {
        this.vgrupo2=result;
        this.idsalon   =this.navParams.data.record.idsalon.id +','+this.navParams.data.record.idsalon.nombre;
        this.idsalon2=this.idsalon.split(',');
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });


       this.horario  =this.navParams.data.record.idhorario
       this.idmateria=this.navParams.data.record.idmateria
       this.capacidad=this.navParams.data.record.capacidad
       this.asignados=this.navParams.data.record.asignados
       this.fexamen  =this.navParams.data.record.fexamen
       this.codfac  =this.navParams.data.record.codfac
       

       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
  let asigna=0;
  
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    asigna=0;
    }
    else
    {

      asigna=this.form.controls['asignados'].value;
    } 
 
     let   options       : any	     = { 
      idtipounidad : {id:this.idtipo,nombre:this.idnombre},  
      idunidadacademica : {id:this.idtipo2,nombre:this.idnombre2,codigo:this.idcodigo2},  
      idperiodo : {id:this.idtipo3,nombre:this.idnombre3},  
      idedificio : {id:this.idedificio2[0],nombre:this.idedificio2[1]},  
      idsalon : {id:this.idsalon2[0],nombre:this.idsalon2[1]},  
     idhorario:this.form.controls['horario'].value,
     nombre:this.idedificio2[1] + ' ' +this.idsalon2[1] + ' ' +this.form.controls['horario'].value + ' ' + this.form.controls['fexamen'].value,
     
     idmateria:this.idmateria,
     capacidad : this.form.controls['capacidad'].value,
     asignados:asigna,
     fexamen:this.form.controls['fexamen'].value,
     codfac : this.form.controls['codfac'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' Planificacion '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'unidadplans').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadplans').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  onSelectChange(selectedValue: any) {
      this.idedificio2=selectedValue.split(',');
      this.authService.getReg(this.idtipo+'/'+ this.idtipo2+'/'+  this.idedificio2[0],'unidadedificiosalons').then((result) => {
        this.vgrupo2=result;
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
        }

        
  onSelectChange2(selectedValue: any) {

    

    // this.iddepartamento=aa[0]
      this.idsalon2=selectedValue.split(',');
      for (var i = 0; i < this.vgrupo2.length; i++) {
        if(this.vgrupo2[i]._id==this.idsalon2[0])
        {
          this.tcapacidad=this.vgrupo2[i].capacidad
          this.form.controls['capacidad'].setValue(this.tcapacidad);
          
            
          

        }
      }
      
  
        }

        
  onSelectChange3(selectedValue: any) {

    

    // this.iddepartamento=aa[0]
      this.idmateria=selectedValue;
  
        }
}



